import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./components/Home/Home";
import Signin from "./components/Authentication/Signin";
// import VerifyOtp from "./components/Authentication/VerifyOtp";
// import SignUp from "./components/Authentication/Signup";
// import ForgetPassword from "./components/Authentication/ForgetPassword";
// import ResetPassword from "./components/Authentication/ResetPassword";
// import QuizGetStarted from "./components/Quiz/QuizGetStarted";
// import Quiz from "./components/Quiz/Quiz";
import Feed from "./components/Feed/Feed";
import Auth from "./components/Authorization/Auth";
 import UpdateDOB from "./components/ContactDetails/UpdateDob";
import UpdateContact from "./components/ContactDetails/UpdateContact";
import UpdateGender from "./components/ContactDetails/UpdateGender";
const App = () => {
  return (
    <>
      <Routes>
        {/* Home Page */}
        <Route path="/" element={<Home />} />

        {/* Signin Page */}
        <Route path="/signin" element={<Signin />} />

        {/* Update Contact Page */}
        <Route path="/contact/:userId" element={<UpdateContact />} />

        {/* Update DOB Page */}
        <Route path="/dob/:userId" element={<UpdateDOB />} />

        {/* Update Gender Page */}
        <Route path="/gender/:id" element={<UpdateGender/>} />

        {/* Feed Page */}
        <Route path="/feed" element={<Auth><Feed /></Auth>} />

        {/* Additional routes */}
        {/* <Route path="/signup" element={<SignUp />} /> */}
        {/* <Route path="/verify-otp" element={<VerifyOtp />} /> */}
        {/* <Route path="/forgot-password" element={<ForgetPassword />} /> */}
        {/* <Route path="/reset-password" element={<ResetPassword />} /> */}
        {/* <Route path="/quiz-start" element={<Auth><QuizGetStarted /></Auth>} /> */}
        {/* <Route path="/quiz" element={<Auth><Quiz /></Auth>} /> */}
      </Routes>
    </>
  );
};

export default App;
